:root {
    /* COLORS */
    --white: #f3f3f3;
    --black: #333333;
    --light-grey: #E2E2E2;
    --grey: #999999; 

    /* DIMENSIONS */
    --base-font-size: 10px;
    --mobile: 0;
    --mobile-landscape: 700px;
    --tablet: 1024px;
    --desktop: 1200px;
    
    /* MEDIA QUERIES */
    @custom-media --mobile-viewport (min-width: 0px);
    @custom-media --mobile-landscape-viewport (min-width: 700px);
    @custom-media --tablet-viewport (min-width: 1024px);
    @custom-media --desktop-viewport (min-width: 1200px);
}

/* RESETS */
html, body {
    margin: 0;
    padding: 0;
    font-size: var(--base-font-size);
}

body {
    max-width: var(--tablet);
    margin: 0 auto;
}@media (--mobile-landscape-viewport) {
    .row {
        width: 100%;
        display: table;
        table-layout: fixed;
    }
    .col {
        display: table-cell;
        vertical-align: top;
    }
    .col-33 {
        width: 33%;
    }
}* {
    font-family: "Trebuchet MS", serif; /* (e.g., Helvetica) */
    font-size: 1.4rem;
     color: var(--black);
}

h1 {
    font-size: 2rem;
    text-transform: uppercase;
}

h2 {
    font-size: 1.8rem;
}

h3 {
    font-size: 1.5rem;
}

p {
    font-size: 1.4rem;
}.content {
    margin:0;
    padding:2rem;
}

 @media (--mobile-landscape-viewport) {
    .content {
        padding:0 2rem 2rem;
    }
 }.menu {
    text-transform: uppercase;
    letter-spacing: 2px;
}

.menu button.hamburger {
    text-align: right;
    width:100%;
    cursor:pointer;
}

.menu button.hamburger:before { 
    content:'\2630';
}

.menu ul {
    margin: 0;
    padding: 0;
    list-style: none;
    display: none;
}

.menu ul li a {
    font-size: 1.2rem;
    line-height: 4rem;
    display: block;
    text-decoration: none;
    text-indent: 2rem;
    color: var(--black);
    background-color: var(--light-grey);
}

.menu ul li a:hover {
    color: var(--white);
    background-color: var(--black);
}

@media (--mobile-landscape-viewport) {

    .menu ul {
        display: block;
    }

    .menu button.hamburger {
        display: none;
    }

}.header {
    text-align: center;
    border-bottom:1rem solid var(--grey);
    font-size: 5rem;
    padding:2rem;
}.logo {
    padding: 10px;
}